*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: linear-gradient(to top left, purple, blue);
}

.scoreboardcontainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  gap: 10px;
}

.itemlist {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 60vh;
  margin-top: 20px;
}

.itemlist table {
  border-collapse: collapse;
}

.itemlist img {
  object-fit: contain;
  width: 100px;
  height: 100px;
}

.itemlist tr > th,
td {
  font-size: 1.5rem;
  border-bottom: 2px solid grey;
  padding-left: 30px;
  padding-right: 30px;
  text-align: LEFT;
}

.itemlsit tr > td {
  border-right: 1px solid grey;
  border-left: 1px solid grey;
}

.itemlist tr > th {
  color: white;
  background-color: black;
}

