.itemlist .item {
  background-color: black;
}

.itemlist tr {
  background-color: white;
}

.itemlist td {
  border-left: 1px solid grey;
  border-right: 1px solid grey;
}

.itemlist tr:last-child td {
  border-bottom: none;
}

.itemlist tr:last-child td:first-child {
  border-left: none;
}

.itemlist tr:last-child td:last-child {
  border-right: none;
}

.itemlist tr:nth-child(2n + 1) {
  background-color: lightblue !important;
}

.itemlist tr:nth-child(1) {
  background-color: gold !important;
}

.itemlist tr:nth-child(2) {
  background-color: silver !important;
}

.itemlist tr:nth-child(3) {
  background-color: #cd7f32 !important;
}

.itemlist tr:first-child th:first-child {
  border-top-left-radius: 10px;
}
.itemlist tr:first-child th:last-child {
  border-top-right-radius: 10px;
}
.itemlist tr:last-child td:first-child {
  border-bottom-left-radius: 10px;
}
.itemlist tr:last-child td:last-child {
  border-bottom-right-radius: 10px;
}

.selectionmenu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  min-width: 30vw;
  min-height: 50px;
}

.selectionmenu .menubutton {
  /*width: 100%;*/
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  color: white;
  border: 2px solid white;
  transition: background-color 0.5s;
}

.selectionmenu .menubutton:first-child {
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.selectionmenu .menubutton:last-child {
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.selectionmenu button {
  background-color: transparent;
  color: white;
  font-size: 1.5em;
  border: none;
  border-right: 1px solid white;
  border-left: 1px solid white;
  margin: none;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
}

.selectionmenu button:last-child {
  border-right: none;
}

.selectionmenu button:first-child {
  border-left: none;
}

.selectionmenu .menubutton:hover {
  background-color: gray;
}

.selectionmenu .selectedbutton {
  background-color: white;
}

.selectionmenu .selectedbutton button {
  color: purple;
}

h1 {
  color: white;
}

.title {
  font-size: 4em;
  color: white;
}
